<template>
  <div id="choicemenu">
   <ul>
     <li :class="{active:activeState==menu.id}" @click="activeState=menu.id" v-for="(menu,index) in menus" :key="index">
       <router-link :to="menu.url">
         <Icon :type="menu.icon"></Icon>
         {{menu.title}}
       </router-link>
     </li>
   </ul>
  </div>
</template>

<script>
export default {
  name: 'ChoiceMenu',
  data () {
    return {
      activeState: 1
    }
  },
  props: {
    menus: {}
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
#choicemenu{
  li{
    font-size: 15px;border: 1px solid #ccc;margin: 10px;
    a{
      width: 100%;
      height:100%;
      padding:5px 10px;
      display: inline-block;
    }
    i{
      font-size: 25px;
    }
    &:hover{
      background: #eee;
    }
  }
  .active{
    background: #eee;
  }
}
</style>
